body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.previousResult {
  height: 60px;
  background-color: white;
  width: 100%;
  font-size: 40px;
  text-align: right;
}

.result {
  height: 60px;
  background-color: white;
  outline-style: solid;
  outline-color: grey;
  width: 100%;
  font-size: 40px;
  text-align: right;
}

.calculator-body {
  outline-style: solid;
  outline-color: black;
  max-width: 600px;
  min-width: 600px;
  margin: auto;
}

button {
  width: 25%;
  height: 60px;
  font-size: 30px;
}

h1 {
  text-align: center;
}

@media only screen and (max-width: 600px) {
  .calculator-body {
    width: 100%;
    height: 100%;
  }
}
